<template>
    <div v-el:wrapper :style="{ height: wrapperHeight + 'px' }">
      <ul v-infinite-scroll="loadMore()" infinite-scroll-disabled="loading" infinite-scroll-distance="5">
        <li v-for="item in list" class="page-infinite-listitem">{{ item | json }}</li>
      </ul>
      <hr>
      <slot></slot>
      Loading: {{ loading }} <br>
      allLoaded: {{ allLoaded }}
      <p v-show="loading">
        ...
      </p>

    </div>
</template>

<script type="text/babel">
  //  mint-ui 
  // http://mint-ui.github.io/docs/#!/zh-cn
  import Vue from 'vue'
  import { InfiniteScroll } from 'mint-ui'
  Vue.use(InfiniteScroll)

  export default {
    props: {
      list: {
        type: Array,
        required: true
      },
      loading: {
        type: Boolean,
        required: true
      },
      allLoaded: {
        type: Boolean,
        required: true
      }
    },

    data() {
      return {
        // list: [],
        // loading: false,
        // allLoaded: false,
        wrapperHeight: 0
      };
    },

    methods: {
      loadMore() {
        if (!this.allLoaded) {
          setTimeout(() => this.$dispatch('load-more'), 200)
        }
      }
    },

    components: {
      InfiniteScroll
    },

    // compiled() {
    //   for (let i = 1; i <= 20; i++) {
    //     this.list.push(i);
    //   }
    // },

    ready() {
      this.wrapperHeight = document.documentElement.clientHeight - this.$els.wrapper.getBoundingClientRect().top;
    }
  };
</script>



